import { useNavigate } from "react-router-dom";
import React, { useEffect, useState } from "react";
import { request } from "src/libs/request";
import { useDaumPostcodePopup } from "react-daum-postcode";
import { useDispatch, useSelector } from "react-redux";
import {
  createEmail,
  createNick,
  createPWord,
  createName,
  createPhone,
  createZipcode,
  createAdr,
  createDeAdr,
  createTerm,
  removeInfo,
} from "src/store/regi_user";
import Logo from "./Logo";

const JoinPage0 = () => {
  const dispatch = useDispatch();
  const user = useSelector((state) => {
    return state.regi_user;
  });

  const [serviceTermsId, setServiceTermsId] = useState(0);
  const [serviceAgree, setServiceAgree] = useState("");
  const [infoTermsId, setInfoTermsId] = useState(0);
  const [infoAgree, setInfoAgree] = useState("");
  const [checkItem, setCheckItem] = useState([]);

  const handleSingleCheck = (checked, id) => {
    if (checked) {
      setCheckItem([...checkItem, id]);
      dispatch(createTerm(id));
    } else {
      setCheckItem(checkItem.filter((el) => el != id));
    }
  };

  const handleAllCheck = (checked) => {
    if (checked) {
      setCheckItem([serviceTermsId, infoTermsId]);
      // setCheckItem(["user-join-service-agree", "user-join-info-agree"]);
      dispatch(createTerm(serviceTermsId));
      dispatch(createTerm(infoTermsId));
    }
  };

  useEffect(() => {
    request.get("/terms/service").then((response) => {
      setServiceAgree(response.data.Terms.content);
      setServiceTermsId(response.data.Terms.id);
    });
    request.get("/terms/privacy").then((response) => {
      setInfoAgree(response.data.Terms.content);
      setInfoTermsId(response.data.Terms.id);
    });
  }, []);

  const navigate = useNavigate();
  return (
    <div className="logo-wrapper">
      <Logo />
      <div className="joinpage0">
        <h2 className="margin-bottom"></h2>
        <div className="margin-bottom left">
          <input
            id="user-join-all-agree"
            type="checkbox"
            className="join-agree-input"
            onChange={(e) => {
              handleAllCheck(e.target.checked);
            }}
            checked={checkItem.length === 2 ? true : false}
          />
          <label for="user-join-all-agree"></label>
        </div>
        <div className="margin-bottom">
          <div className="left">
            <input
              id="user-join-service-agree"
              type="checkbox"
              className="join-agree-input"
              onChange={(e) => {
                handleSingleCheck(e.target.checked, serviceTermsId); // "user-join-service-agree");
              }}
              checked={
                checkItem.includes(serviceTermsId /*"user-join-service-agree"*/)
                  ? true
                  : false
              }
            />
            <label for="user-join-service-agree"> </label>
          </div>
          <div>
            <div className="join-div">{serviceAgree}</div>
          </div>
        </div>
        <div className="margin-bottom">
          <div className="left">
            <input
              id="user-join-info-agree"
              type="checkbox"
              className="join-agree-input"
              onChange={(e) => {
                handleSingleCheck(e.target.checked, infoTermsId); //"user-join-info-agree");
              }}
              checked={
                checkItem.includes(infoTermsId /*"user-join-info-agree" */)
                  ? true
                  : false
              }
            />
            <label for="user-join-info-agree"> </label>
          </div>
          <div>
            <div className="join-div">{infoAgree}</div>
          </div>
        </div>
        <div className="margin-bottom">
          <button
            className="join-btn"
            onClick={() => {
              checkItem.length === 2
                ? navigate("/landing/join/1")
                : alert(" .");
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

const JoinPage1 = () => {
  const navigate = useNavigate();

  const dispatch = useDispatch();
  const user = useSelector((state) => {
    return state.regi_user;
  });

  const [pwordCheck, setPwordCheck] = useState("");
  const [email, setEmail] = useState("");
  const [emailcheck, setEmailCheck] = useState(false);
  const [nickDup, setNickDup] = useState(null);
  const [nickCount, setNickCount] = useState(0);

  const checkEmail = () => {
    var regExp =
      /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;
    if (regExp.test(email)) {
      setEmailCheck(true);
      dispatch(createEmail(email));
    } else setEmailCheck(false);
  };

  useEffect(() => {
    checkEmail();
  }, [email]);

  const onChangeNickNmae = (e) => {
    const regex = /^[-|-|a-z|A-Z|0-9|]+$/;
    regex.test(e.target.value) == true
      ? dispatch(createNick(e.target.value))
      : dispatch(createNick(""));
    if (e.target.value == "") {
      setNickCount(0);
    }
  };

  const dbCheck = () => {
    setNickCount(nickCount + 1);
    request
      .get("/user/available", {
        params: {
          nick: user.nick,
        },
      })
      .then((response) => {
        if (response.data) {
          setNickDup(true);
        } else {
          setNickDup(false);
        }
      });
  };

  const onChangePWord = (e) => {
    dispatch(createPWord(e.target.value));
  };

  const onChangePWordCheck = (e) => {
    setPwordCheck(e.target.value);
  };

  const onClickNext = (e) => {
    user.email == "" ||
    user.nick == "" ||
    user.password == "" ||
    pwordCheck == ""
      ? alert("  .")
      : nickCount == 0
      ? alert("   .")
      : nickDup == false
      ? e.stopPropagation()
      : user.password != pwordCheck
      ? alert("  .")
      : navigate("/landing/join/2");
  };

  const handleOnKeyPress = (e) => {
    if (e.key === "Enter") {
      onClickNext();
    }
  };

  const red = {
    color: "red",
  };

  const green = {
    color: "green",
  };

  return (
    <div className="logo-wrapper">
      <Logo />
      <div className="joinpage1">
        <h2 className="margin-bottom"></h2>
        <p className="left">* </p>
        <div className="margin-bottom">
          <input
            id="user-join-email"
            type="email"
            placeholder=""
            className="join-input"
            maxLength={50}
            // value={user.email}
            // onChange={onChangeEmail}
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            // onBlur={checkEmail}
            autoFocus
          />
          {emailcheck ? (
            <p className="nick-style" style={green}>
               .
            </p>
          ) : (
            <p className="nick-style" style={red}>
                .
            </p>
          )}
        </div>
        <div>
          <input
            id="user-join-nickname"
            placeholder=""
            className="join-nickname-input"
            maxLength={10}
            value={user.nick}
            onChange={onChangeNickNmae}
          />
          <button className="join-nickname-btn" onClick={dbCheck}>
            
          </button>
        </div>
        {nickCount == 0 ? null : nickDup === true ? (
          <p className="nick-style" style={green}>
             .
          </p>
        ) : (
          <p className="nick-style" style={red}>
              .
          </p>
        )}
        <div className="margin-bottom margin-top">
          <input
            id="user-join-password"
            type="password"
            placeholder=""
            className="join-input"
            maxLength={50}
            value={user.password}
            onChange={onChangePWord}
          />
        </div>
        <div className="margin-bottom">
          <input
            id="user-join-password-check"
            type="password"
            placeholder=" "
            className="join-input"
            maxLength={50}
            value={pwordCheck}
            onChange={onChangePWordCheck}
            onKeyPress={handleOnKeyPress}
          />
        </div>
        <div className="margin-bottom">
          <button className="join-btn" onClick={onClickNext}>
            
          </button>
        </div>
      </div>
    </div>
  );
};

const JoinPage2 = () => {
  const navigate = useNavigate();

  const dispatch = useDispatch();
  const user = useSelector((state) => {
    return state.regi_user;
  });

  const [name, setName] = useState("");
  const [nameCheck, setNameCheck] = useState(false);
  const [phone, setPhone] = useState("");
  const [phoneCheck, setPhoneCheck] = useState(false);

  const checkName = () => {
    const regex = /^[-|-]+$/;
    if (regex.test(name)) {
      dispatch(createName(name));
      setNameCheck(true);
    } else {
      setNameCheck(false);
    }
  };

  useEffect(() => {
    checkName();
  }, [name]);

  const checkPhone = () => {
    const regex = /^[0-9]+$/;
    if (regex.test(phone)) {
      dispatch(createPhone(phone));
      setPhoneCheck(true);
    } else {
      setPhoneCheck(false);
    }
  };

  useEffect(() => {
    checkPhone();
  }, [phone]);

  const open = useDaumPostcodePopup();
  const handleComplete = (data) => {
    let fullAddress = data.address;
    let extraAddress = "";

    if (data.addressType === "R") {
      if (data.bname !== "") {
        extraAddress += data.bname;
      }
      if (data.buildingName !== "") {
        extraAddress +=
          extraAddress !== "" ? `, ${data.buildingName}` : data.buildingName;
      }
      fullAddress += extraAddress !== "" ? ` (${extraAddress})` : "";
    }
    // console.log(fullAddress); // e.g. '  2 20 (1)'

    dispatch(createZipcode(data.zonecode));
    dispatch(createAdr(data.address));
  };

  const handleClick = () => {
    open({ onComplete: handleComplete });
  };

  const onChangeDeAdr = (e) => {
    dispatch(createDeAdr(e.target.value));
  };

  const handleJoin = () => {
    request.post("/user/register", {
      create: {
        email: user.email,
        password: user.password,
        name: user.name,
        nick: user.nick,
        phone: user.phone,
        zipcode: user.zipcode,
        address: user.address,
        address_detail: user.address_detail,
      },
      terms: user.terms,
    });
    navigate("/landing/join/3");
  };

  const handleOnKeyPress = (e) => {
    if (e.key === "Enter") {
      handleJoin();
    }
  };

  const red = {
    color: "red",
  };

  const green = {
    color: "green",
  };

  return (
    <div className="logo-wrapper">
      <Logo />
      <div className="joinpage2">
        <h2 className="margin-bottom"></h2>
        <p className="left">* </p>
        <div className="margin-bottom">
          <input
            id="user-join-name"
            placeholder=""
            className="join-input"
            maxLength={10}
            value={name}
            onChange={(e) => {
              setName(e.target.value);
            }}
            autoFocus
          />
          {nameCheck ? (
            <p className="nick-style" style={green}>
               .
            </p>
          ) : (
            <p className="nick-style" style={red}>
                .
            </p>
          )}
        </div>
        <div className="margin-bottom">
          <input
            id="user-join-phone"
            placeholder=""
            className="join-input"
            maxLength={13}
            value={phone}
            onChange={(e) => {
              setPhone(e.target.value);
            }}
          />
          {phoneCheck ? (
            <p className="nick-style" style={green}>
               .
            </p>
          ) : (
            <p className="nick-style" style={red}>
                .
            </p>
          )}
        </div>
        <div className="margin-bottom">
          <input
            id="user-join-postcode"
            placeholder=""
            className="join-postcode-input"
            value={user.zipcode}
          />
          <button className="join-postcode-btn" onClick={handleClick}>
            
          </button>
        </div>
        <div className="margin-bottom">
          <input
            id="user-join-adress"
            placeholder=""
            className="join-div"
            value={user.address}
          />
        </div>
        <div className="margin-bottom">
          <textarea
            id="user-join-adress-detail"
            placeholder=""
            className="join-adress"
            maxLength={50}
            value={user.address_detail}
            onChange={onChangeDeAdr}
            onKeyPress={handleOnKeyPress}
          />
        </div>
        <div className="margin-bottom">
          <button
            className="join-btn"
            onClick={() => {
              handleJoin();
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

const JoinPage3 = () => {
  const navigate = useNavigate();

  const dispatch = useDispatch();
  const user = useSelector((state) => {
    return state.regi_user;
  });

  useEffect(() => {
    return () => {
      dispatch(removeInfo());
    };
  });

  return (
    <div className="logo-wrapper">
      <Logo />
      <div className="joinpage3">
        <h2 className="margin-bottom"></h2>
        <div className="left">
          <p>{user.nick}  .</p>
          <p>{user.email}   .</p>
          <p>     .</p>
        </div>
        <div className="margin-bottom">
          <button
            className="join-btn"
            onClick={() => {
              navigate("/landing/login");
            }}
          >
            
          </button>
        </div>
        <div className="margin-bottom">
          <button className="join-btn">   </button>
        </div>
        <div className="margin-bottom">
          <button
            className="join-btn"
            onClick={() => {
              navigate("/");
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

export { JoinPage0, JoinPage1, JoinPage2, JoinPage3 };
